<template>
	<view>
			<image class="ktv" mode="aspectFit" src="../../static/ktv.png" @click="scan()"></image>
	</view>
</template>

<script>
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				
			};
		},
		methods:{
			scan(){
				let vue=this;
				uni.scanCode({
						onlyFromCamera: true,
						success: function (res) {
							console.log('条码类型：' + res.scanType);
							console.log('条码内容：' + res.result);
							
							uni.request({
								method:'POST',
								url:'http://localhost:8080/room/searchRoomById',
								data:res.result,
								success: (res) => {
									console.log(res)
									console.log(!res.data.data.roomIsopen)
									if(!res.data.data.roomIsopen){
										vue.$store.state.room=res.data.data;
										uni.navigateTo({
											url:'../openRoom/openRoom'
										})
									}else{
										uni.navigateTo({
											url:'../index/index'
										})
									}
									// this.$store.state.roomid=res.data.data
								}
							})
						}
				})
			}
		},
		mounted(){
			
		}
	}
</script>

<style lang="scss">
.ktv{
	width: 320px;
	height: 568px;
}
</style>
